import { useState, useEffect, useRef, useLayoutEffect } from 'react';

import { roundFlat } from 'utils/tools';
import {
  Main_Padding,
  Progress_Bar_H,
  input_Row_H,
} from 'style/style-parameters';

import { ProgressBar, TextPositionHolder } from './style';
import { CharactarStyles, Position_Persent } from './constans';

const Charactar = ({
  relativeIndex,
  targetCharactar,
  classNames,
}: {
  relativeIndex: number;
  targetCharactar: string;
  classNames: CharactarStyles[];
}) => (
  <div
    id={relativeIndex === 0 ? 'current-target' : ''}
    className={['charactar-cube', ...classNames].join(' ')}
  >
    {targetCharactar}
  </div>
);

const CharactarList = ({
  targetString,
  progressNumber,
  inputContent,
  lastMatchNumber = 0,
  skipped = [],
  type,
}: {
  targetString: string;
  progressNumber: number;
  inputContent: string;
  lastMatchNumber?: number;
  skipped?: number[];
  type: 'target' | 'input';
}) => {
  const className = 'charactar-list one-line';
  return (
    <div className={className}>
      {targetString.split('').map((targetCharactar, index) => {
        const relativeIndex = index - progressNumber;
        const inputCharactar =
          relativeIndex < 0
            ? ''
            : inputContent.slice(relativeIndex, relativeIndex + 1);
        let classNames: string[] = [];
        if (type === 'target') {
          // 上方
          if (relativeIndex < 0) {
            classNames = [CharactarStyles.hidden];
          } else if (!inputCharactar) {
            classNames = [CharactarStyles.default];
          } else if (inputCharactar === targetCharactar) {
            classNames = [CharactarStyles.match];
          } else {
            classNames = [CharactarStyles.notmatch];
          }
          if (skipped.includes(index)) {
            classNames = [CharactarStyles.skipped, CharactarStyles.default];
          }
        } else {
          // 下方
          if (skipped.includes(index)) {
            classNames = [CharactarStyles.hidden, CharactarStyles.skipped];
          } else {
            classNames = [CharactarStyles.passed];
          }
        }
        // 上下通用
        if (index >= progressNumber - lastMatchNumber && relativeIndex < 0) {
          classNames.push(CharactarStyles.lastMatch);
        }
        const props = {
          relativeIndex,
          targetCharactar,
          inputCharactar,
          classNames,
        } as any;
        return <Charactar key={index} {...props} />;
      })}
    </div>
  );
};

interface iTypingBox {
  progressNumber: number;
  targetString: string;
  skippedList: any[];
  onPaste?: (e: any) => any;
  onPause: any;
  inputContent: any;
  lastMatchString: any;
  paused: boolean;
  inputRef: any;
  handleInputChange: any;
  isInfinity?: boolean;
}

export const TypingBox = ({
  progressNumber,
  targetString,
  skippedList,
  onPaste = (_) => {},
  onPause,
  inputContent,
  lastMatchString,
  paused,
  inputRef,
  handleInputChange,
  isInfinity = false,
}: iTypingBox) => {
  const scrollContainerRef = useRef<any>();
  const targetStringRef = useRef<any>();
  const nextZiRef = useRef<any>();
  const [inputAreaPositionX, updateInputAreaPositionX] = useState(0);
  const [inputWidth, updateInputWidth] = useState(300);

  // TODO 彻底虚拟化文字列表，以解决位置抖动的问题
  useLayoutEffect(() => {
    nextZiRef.current = document.querySelector(
      '#current-target'
    ) as HTMLElement;
    if (
      scrollContainerRef.current &&
      nextZiRef.current &&
      targetStringRef.current
    ) {
      const containerW = scrollContainerRef.current.offsetWidth;
      const lastZi = targetStringRef.current.lastChild.lastChild;
      const maxW = lastZi.offsetLeft + lastZi.offsetWidth + Main_Padding;
      const mostFarX = containerW * Position_Persent[1] - maxW;
      const nextZiFormLeft = Main_Padding + nextZiRef.current.offsetLeft;

      const x1 = Position_Persent[0] * containerW - nextZiFormLeft;
      const x2 = Math.max(x1, mostFarX);
      const x3 = Math.min(x2, 0);

      const inputW = Math.max(
        0,
        containerW - x3 - nextZiRef.current.offsetLeft - 1
      );

      const newPosition = roundFlat(3)(x3);
      if (newPosition !== inputAreaPositionX) {
        updateInputAreaPositionX(newPosition);
        updateInputWidth(roundFlat(3)(inputW));
      }
    }
  });

  const handlePause = () => {
    onPause();
  };

  const finished = progressNumber >= targetString.length;
  const inputPlaceHolder = finished
    ? '（完）'
    : paused
    ? '已暂停计时'
    : progressNumber === 0
    ? '在这里输入'
    : '';

  return (
    <div ref={scrollContainerRef} className="input-area">
      <ProgressBar
        height={Progress_Bar_H}
        progress={isInfinity ? 0 : (100 * progressNumber) / targetString.length}
      />
      <div
        style={{
          position: 'absolute',
          top: '0',
          left: `${inputAreaPositionX}px`,
          zIndex: 100,
          height: `${2 * input_Row_H + Progress_Bar_H}`,
        }}
      >
        <div ref={targetStringRef} className="target-string">
          <CharactarList
            type="target"
            skipped={skippedList}
            targetString={targetString}
            progressNumber={progressNumber}
            inputContent={inputContent}
            lastMatchNumber={lastMatchString.length}
          />
        </div>
        <div className="input-box">
          <CharactarList
            type="input"
            skipped={skippedList}
            targetString={targetString.slice(0, progressNumber)}
            progressNumber={progressNumber}
            inputContent={inputContent}
            lastMatchNumber={lastMatchString.length}
          />
          <TextPositionHolder>/</TextPositionHolder>
          <input
            role="presentation"
            autoComplete="off"
            style={{
              width: `${inputWidth}px`,
            }}
            disabled={finished}
            type="text"
            ref={inputRef}
            placeholder={inputPlaceHolder}
            onChange={handleInputChange}
            onPaste={(e) => {
              onPaste(e);
              return e.preventDefault();
            }}
            onBlur={handlePause}
          />
        </div>
      </div>
    </div>
  );
};
