import { Color_Main_bg } from 'style/style-parameters';
import { createGlobalStyle } from 'styled-components';
import { globalFontSize } from './style-parameters';
import image_bg1 from '_assets/image/bg1.png';

export const GlobalStyle = createGlobalStyle<any>`
  html {
    height: 100%;
    ${(p) => (p.focusMode ? 'filter: brightness(90%);' : '')}
  }

  body {
    overflow: hidden;
    height: 100%;
    margin: 0;
    background-color: #f1f1f1;
    background-image: url(${image_bg1});
    ${p => p.focusMode ? `
    background: ${Color_Main_bg};
    ` : ''}
    font-size: ${globalFontSize};
  }

  #root {
    height: 100%;
  }

  input {
    font-size: ${globalFontSize};
  }

  textarea {
    font-size: ${globalFontSize};
  }
`;
