import { useState, useEffect, useRef, useContext, Dispatch } from 'react';
import { debounce, throttle } from 'lodash';

export const usePrevious = (value: any) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  }, [value]);
  return ref.current;
};

export const useUpdateEffect = (effect: () => any, deps: any[]) => {
  const isInitialMount = useRef(true);

  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
    } else {
      return effect();
    }
  }, deps);
};

export const useToggle = (
  defaultStatus = false,
  wait = 200
): [boolean, () => void, any] => {
  const [status, setStatus] = useState(defaultStatus);
  const toggleStatus = throttle(() => {
    setStatus(!status);
  }, wait);
  return [status, toggleStatus, setStatus];
};

export const useBebounceRenderSeed = (deps: any[], wait = 1000) => {
  const [renderSeed, updateRenderSeed] = useState(0);
  useEffect(
    debounce(() => {
      try {
        if (renderSeed < 99999) {
          updateRenderSeed(renderSeed + 1);
        } else {
          updateRenderSeed(0);
        }
      } catch (e) {
        // do nothing
      }
    }, wait),
    deps
  );
  return renderSeed;
};

export const useTimeout = (clear = false) => {
  const timer = useRef<any>();
  useEffect(
    () => () => {
      clearTimeout(timer.current);
    },
    []
  );
  return (action: () => void, wait = 0) => {
    clear && clearTimeout(timer.current);
    timer.current = setTimeout(action, wait);
  };
};
