import { useState, useContext, useMemo, useEffect } from 'react';
import { MainContext } from 'context/main-context';
import {
  CommonTextContent,
  GhostSeparatorV,
  GroupBox,
  PartInfo,
  PartTitle,
} from 'style/universilal-style';

import {
  DataBar,
  DataBox,
  StatisticsCommonText,
  StyledStatistics,
  ZiList,
} from './style';
import { checkRawData, getDataFromRaw, getZiAvgTime } from 'utils/data';
import { ProgressBar } from 'components/main-typing-box/style';
import { FrequencyGraph } from './frequency-graph';
import { debounce } from 'lodash';
import { render } from 'react-dom';
import { useBebounceRenderSeed } from 'hooks/common-hooks';

const sortFun = ([, a]: any, [, b]: any) => getZiAvgTime(b) - getZiAvgTime(a);

const SlowZi = ({ ziData: [zi, rawData] }: any) => {
  return (
    <div className="zi-item">
      {zi}{' '}
      <span className="zi-info">{`${(getZiAvgTime(rawData) / 1000).toFixed(
        1
      )}s`}</span>
    </div>
  );
};

const MostZi = ({ ziData }: any) => {
  const data = getDataFromRaw(ziData[1]);
  return (
    <div className="zi-item">
      {ziData[0]} <span className="zi-info">{`${data.count}`}</span>
    </div>
  );
};

export const Statistics = () => {
  const {
    ziData: [ziDataMap],
  } = useContext<any>(MainContext);

  const renderSeed = useBebounceRenderSeed([ziDataMap], 10 * 1000);

  if (!ziDataMap) {
    return <div className="statistics">暂无数据</div>;
  }

  const listLength = Math.min(20, Math.floor(ziDataMap.size * 0.1));

  const slowestZiList = useMemo(
    () => [...ziDataMap.entries()].sort(sortFun).slice(0, listLength),
    [renderSeed]
  );

  // const mostZiList = useMemo(
  //   () =>
  //     [...ziDataMap.entries()]
  //       .filter(
  //         ([, rawData]) =>
  //           checkRawData(rawData) && getDataFromRaw(rawData).count > 1
  //       )
  //       .sort(
  //         ([, a], [, b]) => getDataFromRaw(b).count - getDataFromRaw(a).count
  //       )
  //       .slice(0, listLength),
  //   [renderSeed]
  // );

  const inputZiCount = [...ziDataMap.entries()].reduce(
    (sum, [, rawData]) => getDataFromRaw(rawData).count + sum,
    0
  );

  const allCount = (
    <StatisticsCommonText>
      <span>累计已输入 {inputZiCount} 字</span>
    </StatisticsCommonText>
  );

  const slowZi =
    slowestZiList.length === 0 ? null : (
      <>
        <GhostSeparatorV />
        <PartTitle>{'用时最长的字'}</PartTitle>
        <PartInfo>「输入用时估计值」最长的字</PartInfo>
        <ZiList>
          {slowestZiList.map((ziData, index) => (
            <SlowZi key={index} ziData={ziData} />
          ))}
        </ZiList>
      </>
    );

  const distribution = useMemo(
    () => (
      <>
        <GhostSeparatorV />
        <PartTitle>{'单字用时分布'}</PartTitle>
        <PartInfo>
        「中日韩统一表意文字」共约2万个，已输入过其中的 {ziDataMap.size} 个，下为这{' '}
          {ziDataMap.size}{' '}
          个汉字「输入用时估计值」的分布图，颜色由深到浅分别表示「常用一千字」「国标一级汉字」和「其他汉字」
        </PartInfo>
        <FrequencyGraph data={ziDataMap}></FrequencyGraph>
      </>
    ),
    [renderSeed]
  );

  const explanation = (
    <>
      <GhostSeparatorV />
      <PartTitle>{'关于「输入用时估计值」'}</PartTitle>
      <PartInfo>
        {'用任何文本练习，每当汉字匹配时都会更新对应字的「输入用时估计值」。' +
          '「估计值」受所有历史输入的影响，但会更接近最近几次输入的用时。' +
          '所有涉及「慢速字」的文本，都是依据「估计值」生成的。'}
      </PartInfo>
    </>
  );

  return (
    <StyledStatistics>
      <GhostSeparatorV />
      {allCount}
      {distribution}
      {slowZi}
      {explanation}
      <GhostSeparatorV />
    </StyledStatistics>
  );
};
