import { FC, useRef, Children, useContext } from 'react';
import { AppSetting } from 'components/app-setting';
import { TypeTraining } from 'components/main-typing-box/type-training';
import { Box } from 'components/shelf/box';
import { Statistics } from 'components/statistics/statistics';
import { TargetSetting } from 'components/text-setting';
import { MainContext } from 'context/main-context';
import { useAutoAnimate } from 'hooks/animation-hooks';
import { useToggle } from 'hooks/common-hooks';
import { GlobalStyle } from 'style/globalStyle';
import { isMobile } from 'utils/tools';
import { StyledApp, MainSeparator, OptionsPart, UIButtons } from './style';
import { Tabs } from 'components/shelf/tabs';

const SortedApp: FC<any> = ({ children, atBottom = false, focusMode }) => {
  const appRef = useRef();
  useAutoAnimate(appRef);
  if (!children) {
    return null;
  }

  const childrenArray = Children.toArray(children);
  if (atBottom) {
    childrenArray.reverse();
  }
  return (
    <StyledApp ref={appRef} atBottom={atBottom} focusMode={focusMode}>
      {childrenArray}
    </StyledApp>
  );
};

const pathMame =
  window.location.pathname === '/' ? '' : window.location.pathname;
const mainSeparator = (
  <MainSeparator>
    {/* Copyright © 2022 4d46, All Rights Reserved. */}
    {window.location.host + pathMame || ''}
  </MainSeparator>
);

export const Main = () => {
  const {
    appSetting: {
      enableAutoRestartToggle: [showAutoRestart],
      autoRestart: [autoRestart, toggleAutoRestart],
    },
  } = useContext<any>(MainContext);
  const [focusMode, toggleFocusMode] = useToggle(false);
  const [isAtBottom, toggleIsAtBottom] = useToggle(isMobile);
  const shouldShowMainSeparator = isAtBottom || (!isAtBottom && !focusMode);
  return (
    <>
      <GlobalStyle focusMode={focusMode} />
      <SortedApp atBottom={isAtBottom} focusMode={focusMode}>
        <>
          <TypeTraining />
          {shouldShowMainSeparator ? mainSeparator : null}
        </>
        {/* <EndBox height={60} up={isAtBottom} /> */}
        {focusMode ? null : (
          <Tabs defaultIndex={0}>
            {[
              { title: '统计', content: <Statistics /> },
              { title: '文本', content: <TargetSetting /> },
              { title: '设置', content: <AppSetting /> },
            ]}
          </Tabs>
        )}
        <UIButtons>
          {!focusMode && showAutoRestart ? (
            <div onClick={toggleAutoRestart}>
              {autoRestart ? '关闭自动重开' : '开启自动重开'}
            </div>
          ) : null}
          <div onClick={toggleFocusMode}>
            {focusMode ? '退出专注模式' : '专注模式'}
          </div>
          {focusMode ? null : <div onClick={toggleIsAtBottom}>反转方向</div>}
        </UIButtons>
      </SortedApp>
    </>
  );
};
