import { Color_Sub_bg_1 } from './../../style/style-parameters';
import { Color_Main_fg_1, Color_Main_fg_2 } from 'style/style-parameters';
import { Header_H, Color_Main_bg, Main_Padding } from 'style/style-parameters';
import styled, { keyframes } from 'styled-components';
import {
  input_Row_H,
  Progress_Bar_H,
  textBoxPadding,
} from 'style/style-parameters';

const lastMatchAnimation = keyframes`
  0% {transform: translate(0, -2em); color: #000;}
  16% {transform: translate(0, 0); color: #000;}
  25% {transform: translate(0, 0); color: #000;}
  100% {transform: translate(0, 0);}
`;

export const TypingArea = styled.div<any>`
  text-align: left;
  /* background-color: ${Color_Sub_bg_1}; */
  overflow: hidden;
  position: relative;
  height: ${2 * input_Row_H + Progress_Bar_H}px;

  * {
    font-weight: 400;
    line-height: ${input_Row_H}px;
  }

  .input-area {
    border: none;
    overflow: hidden;
    width: 100%;
  }

  .target-string {
    box-sizing: border-box;
    display: flex;
    padding: 0 ${textBoxPadding};
    height: ${input_Row_H}px;
  }

  .input-box {
    margin-top: ${Progress_Bar_H}px;
    box-sizing: border-box;
    display: flex;
    text-align: center;
    padding: 0 ${textBoxPadding};
    height: ${input_Row_H}px;

    input {
      background: #f001;
      // height: ${input_Row_H}px;
      margin: 0;
      width: 500px;
      overflow: hidden;
      outline-style: none;
      padding: 0;
      border: none;
      background: transparent;
      color: #0005;
      &::placeholder {
        font-weight: bolder;
        color: #00000032;
        // font-style: italic;
      }
    }
  }

  .charactar-list {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    .charactar-cube {
      text-align: center;
      user-select: none;
    }
    .match {
      color: #00f8;
    }
    .notmatch {
      text-decoration: underline;
      color: #f008;
    }
    .default {
      color: #000;
    }
    .passed {
      color: #0007;
    }
    .last-match {
      background: #0000000a;
      &.passed {
        animation-name: ${lastMatchAnimation};
        animation-duration: 0.6s;
      }
    }
    .hidden {
      color: #0000000a;
    }
    .skipped {
      background: #ff02;
    }
  }

  .buttons {
    display: none;
  }

  #dev-info {
    font-size: 12px;
    line-height: 2em;
    color: #f00;
    position: fixed;
    top: 300px;
    margin: 0 0.5em;
  }
`;

export const ProgressBar = styled.div.attrs<any>((p) => ({
  style: {
    background: `linear-gradient(
      to right, 
      #0006 ${p.progress || 0}%,
      #0002 ${p.progress || 0}%,
      #0002
    )`,
  },
}))<any>`
  position: absolute;
  left: 0;
  top: ${input_Row_H}px;
  height: ${(p) => p.height || Progress_Bar_H}px;
  width: 100%;
`;

export const TypingBoxInfo = styled.div`
  margin-bottom: -3px;
  min-height: 22px;
  line-height: 22px;
  z-index: 100;
  color: #737373;
  font-size: 12px;
  padding: 0 1em;
  display: flex;
  text-align: left;
  flex-wrap: wrap;
  overflow: hidden;
  & > div {
    margin-right: 1em;
  }
  .item-1 {
    color: #444;
  }
`;

