import { roundFlat } from 'utils/tools';
import { isChinese } from './../tools/string';
const ziDataTimeLimit = 1000 * 3600;
const errorTimeFallback = 1000 * 10;

export enum dataVersions {
  unknown = 'unknown',
  v1 = 'v0.01', // ['字', [次数, 累计时间, 时间戳, 估计时间, 'v0.01']]
  v2 = 'v2', // ['字', [次数, Base36时间戳, 估计时间, 'v2']]
}

const getDataVersionFromRaw = (raw: any[]): dataVersions => {
  if (!raw) {
    return dataVersions.unknown;
  }
  if (raw[4] === dataVersions.v1) {
    return dataVersions.v1;
  }
  if (raw[3] === dataVersions.v2) {
    return dataVersions.v2;
  }
  return dataVersions.unknown;
};

export interface iZiData {
  dataVersion: dataVersions;
  count: number; // 累计输入次数
  lastTime: number; // 上次输入的时间戳毫秒数
  estimated: number; // 估计的单次输入耗时
  sumTime?: number; // 累计输入时间（v2之后已废弃）
}

const failData = {
  dataVersion: dataVersions.unknown,
  count: 0,
  lastTime: 0,
  estimated: 0,
};

export const getDataFromRaw = (raw: string[]): iZiData => {
  if (!raw) {
    return failData;
  }
  const version = getDataVersionFromRaw(raw);
  if (version === dataVersions.v1) {
    return {
      dataVersion: dataVersions.v2,
      count: parseInt(raw[0], 10),
      lastTime: parseInt(raw[2], 10),
      estimated: parseInt(raw[3], 10),
    };
  }
  if (version === dataVersions.v2) {
    return {
      dataVersion: dataVersions.v2,
      count: parseInt(raw[0], 10),
      lastTime: parseInt(raw[1], 36) * 1000,
      estimated: parseInt(raw[2], 10),
    };
  }
  // 未匹配版本的数据要清除
  return failData;
};

const currentDataVersion = dataVersions.v2;
export const createRawData = (data: any): string[] => {
  const dataVersion = currentDataVersion;
  const times = data.times.toString();
  const lastTime = Math.floor(data.lastTime / 1000).toString(36);
  const estmated = data.estimated.toString();
  return [times, lastTime, estmated, dataVersion];
};

export const checkRawData = (raw: any) => {
  const dataVersion = getDataVersionFromRaw(raw);
  // 废弃的数据版本
  if (dataVersions.unknown === dataVersion) {
    return false;
  }
  // 异常数据
  const data = getDataFromRaw(raw);
  if (!data || data.estimated > ziDataTimeLimit || data.estimated <= 0) {
    return false;
  }
  // 检测通过
  return true;
};

export const checkMapData = (mapArray: any) => {
  // 不是基本汉字
  if (!isChinese(mapArray[0])) {
    return false;
  }
  // 数据异常
  if (!checkRawData(mapArray[1])) {
    return false;
  }
  // 检测通过
  return true;
};

export const washZiTimeDataArray = (datas: any = []) => {
  let changed = false;
  const newDatas = datas.filter((mapData: any) => {
    const pass = checkMapData(mapData);
    changed = !pass;
    return pass;
  });
  return [newDatas, changed];
};

const TIME_FACTOR = 0.93;
export const calcNewZiTimeData = (
  lastData: iZiData | null,
  ziTime: number,
  now: number
) => {
  const { count = 0, estimated = 0 } = lastData || {};
  const newCount = count + 1;
  const lastEstimated = estimated;
  const weightOfOld =
    newCount > 100 ? TIME_FACTOR * 0.99 : (TIME_FACTOR * count) / (count + 1);
  const newETime = weightOfOld * lastEstimated + (1 - weightOfOld) * ziTime;
  const newData = {
    times: newCount,
    estimated: newCount > 1 ? Math.round(newETime) : ziTime,
    lastTime: now,
    dataVersion: currentDataVersion,
  };
  return newData;
};

export const getZiAvgTime = (ziData: any[]) => {
  const data = getDataFromRaw(ziData);
  const time = data.estimated;
  return time;
};
