import { useState, useContext, useRef, useEffect } from 'react';

import { MainContext } from 'context/main-context';
import {
  getLSCustomSavedText,
  storeLSCustomSavedText,
  getLSLastUsedText,
  storeLSLastUsedText,
} from 'utils/storage';
import { filtBaseChinese, parseContent } from 'utils/tools';
import {
  Button,
  ErrorInfo,
  GhostSeparatorV,
  GroupBox,
  ItemList,
  PartInfo,
  PartTitle,
} from 'style/universilal-style';

import { StyledTargetSetting } from './style';
import { iTextInfoCard } from 'utils/text/baker';
import { targetTextMap } from 'utils/text/text-cards';
import { CheckBox } from 'components-ui';

interface iSavedText {
  title: string;
  text: string;
}

const maxSaveTextNumber = 6;
const saveTextBtnTitle = '保存文本';
const maxTextLength = 3000;
const titleMaxLength = 10;

enum Text_Error {
  Title_Too_Long = 'Title_Too_long',
  Text_Too_Long = 'Text_Too_long',
}

export const TargetSetting = () => {
  const {
    ziData: [ziDataMap],
    textCard: [, setTextCard],
    currentText: [currentText],
  } = useContext<any>(MainContext);

  const [content, updateContent] = useState('');
  const [random, setRandom] = useState<boolean>(false);
  const [auto, setAuto] = useState<boolean>(false);
  const [lastSetText, updateLastSetText] = useState<string>(
    getLSLastUsedText() || ''
  );
  const [savedTextCard, updateSavedTextCard] = useState<iTextInfoCard[]>(
    getLSCustomSavedText() || []
  );
  const [isSavingText, setIsSavingText] = useState(false);
  const [textError, setTextError] = useState<any>(null);

  const inputRef = useRef<any>();

  useEffect(() => {
    storeLSLastUsedText(lastSetText);
  }, [lastSetText]);

  useEffect(() => {
    storeLSCustomSavedText(savedTextCard);
  }, [savedTextCard]);

  const contentRewriter = (fun: (str: string) => string) => () => {
    const newContent = fun(content);
    inputRef.current.value = newContent;
    updateContent(newContent);
  };

  const clearInput = () => {
    inputRef.current.value = '';
    updateContent('');
    setTextError({
      ...textError,
      text: new Set(),
    });
  };

  const inputCurrent = () => {
    const text = currentText || '';
    inputRef.current.value = text;
    updateContent(text);
  };

  const changeToCard = (card: iTextInfoCard) => {
    if (card) {
      // setTargetString('');
      setTextCard({ ...card });
      // setTaregtID(card.id || '');
      // setTargetTitle(card.title || '');
    }
  };

  const selectCurrentText = () => {
    const parsedContent = parseContent()(content);
    clearInput();
    changeToCard({
      id: '',
      text: {
        bricks: parsedContent.split(''),
        random,
        infinity: auto,
      },
    });
    updateLastSetText(parsedContent);
  };

  const handleInputChange = (event: any) => {
    const value = event.target.value;
    const textErrorSet = new Set(textError?.text || []);
    if (value.length > maxTextLength) {
      textErrorSet.add(Text_Error.Text_Too_Long);
    } else {
      textErrorSet.delete(Text_Error.Text_Too_Long);
    }
    setTextError({
      ...textError,
      text: textErrorSet,
    });
    updateContent(value);
  };

  const randomResort = () => {
    const newContent = parseContent(true)(content);
    inputRef.current.value = newContent;
    updateContent(newContent);
  };

  const removeNonChinese = contentRewriter(filtBaseChinese());

  const handleSavedTextBtnClick = (index: number) => () => {
    const card = savedTextCard[index];
    if (!card || !card?.text?.bricks || index + 1 > savedTextCard.length) {
      return;
    }
    changeToCard(card);
    setAuto(false);
    setRandom(false);
  };

  const saveCurrentText =
    (index = Infinity) =>
    () => {
      if (!isSavingText) return;
      const newText: iTextInfoCard = {
        id: '',
        title: textTitle || '',
        text: {
          bricks: content.split(''),
          brickType: 'zi',
          random,
          infinity: auto,
        },
      };
      if (index > savedTextCard.length - 1) {
        const newSavedText: iTextInfoCard[] = [...savedTextCard, newText];
        updateSavedTextCard(newSavedText);
      } else if (index >= 0 && index < savedTextCard.length) {
        const newSavedText: iTextInfoCard[] = [...savedTextCard];
        newSavedText[index] = newText;
        updateSavedTextCard(newSavedText);
      }
      setAuto(false);
      setRandom(false);
    };

  const startSaveCurrentText = () => {
    if (!content) return;
    setIsSavingText(true);
  };

  const exitSavingCurrentText = () => {
    inputTitleRef.current.value = '';
    setTextTitle('');
    setTextError({
      ...textError,
      title: new Set(),
    });
    setIsSavingText(false);
  };

  const useLastInputContent = () => {
    if (!lastSetText) return;
    changeToCard({
      id: '',
      text: {
        bricks: lastSetText.split(''),
      },
    });
  };

  const [textTitle, setTextTitle] = useState<string>();
  const inputTitleRef = useRef<any>();

  const inputTitlePlaceHolder = '输入文本名称';
  const handleTextTitleChange = (event: any) => {
    const title = event.target.value;
    const titleErrorSet = new Set(textError?.title || []);
    if (title.length > titleMaxLength) {
      titleErrorSet.add(Text_Error.Title_Too_Long);
    } else {
      titleErrorSet.delete(Text_Error.Title_Too_Long);
    }
    setTextError({
      ...textError,
      title: titleErrorSet,
    });
    setTextTitle(title);
  };
  const inputPlaceHolder = '在这里输入文本';
  const isTextTooLong = textError?.text?.has?.(Text_Error.Text_Too_Long);
  const isTextTitleTooLong = textError?.title?.has?.(Text_Error.Title_Too_Long);

  const buildinPart = (title: string, info: string) => (
    <>
      <PartTitle>{title}</PartTitle>
      <PartInfo>{info}</PartInfo>
      <ItemList>
        {Array.from(targetTextMap).map(([id, item]) => {
          const noZiData =
            item?.text?.bricks?.length < 1 && ziDataMap?.size < 1;
          const shouldDisable = isSavingText || noZiData;
          return !item.active ? null : (
            <Button
              disabled={shouldDisable}
              key={id}
              onClick={() => {
                changeToCard(item);
              }}
            >
              {item.title}
              {
                <span className="text-length">
                  {item.text?.infinity
                    ? '连续'
                    : item.group === 'generate'
                    ? '自动'
                    : item.text?.random
                    ? '乱序'
                    : item.text?.bricks?.join('').length}
                </span>
              }
            </Button>
          );
        })}
      </ItemList>
    </>
  );

  const userSavedPart = (
    <>
      <PartTitle>保存的文本</PartTitle>
      <PartInfo>
        使用「{saveTextBtnTitle}
        」功能保存的文本，最多可保存 {maxSaveTextNumber} 个
      </PartInfo>
      <ItemList>
        {/* <Button
          onClick={useLastInputContent}
          disabled={isSavingText || !lastSetText}
        >
          上次输入的文本
        </Button> */}
        {savedTextCard.map((textInfo: iTextInfoCard, index: number) => {
          const textIndex = index + 1;
          const button = isSavingText ? (
            // 覆盖已有按钮
            <Button
              className="btn-o"
              key={index}
              onClick={() => {
                saveCurrentText(index)();
                exitSavingCurrentText();
                clearInput();
              }}
              disabled={
                !isSavingText ||
                !textTitle ||
                isTextTooLong ||
                isTextTitleTooLong
              }
            >
              覆盖「{textInfo?.title}」
            </Button>
          ) : (
            // 自定义文本按钮
            <Button key={index} onClick={handleSavedTextBtnClick(index)}>
              {textInfo?.title ||
                `共${
                  textInfo?.text?.bricks?.join('').length || 0
                }字的自定义文本`}
              {textInfo?.text?.bricks?.length > 0 ? (
                <span className="text-length">
                  {textInfo.text.infinity
                    ? '连续'
                    : textInfo.text.random
                    ? '乱序'
                    : `${textInfo?.text?.bricks?.join('').length}`}
                </span>
              ) : null}
            </Button>
          );
          return !textInfo ? null : button;
        })}
        {isSavingText && savedTextCard.length < maxSaveTextNumber ? (
          <Button
            className="btn-o"
            onClick={() => {
              saveCurrentText()();
              exitSavingCurrentText();
              clearInput();
            }}
            disabled={
              !isSavingText || !textTitle || isTextTooLong || isTextTitleTooLong
            }
          >
            添加
          </Button>
        ) : null}
      </ItemList>
    </>
  );

  const inputPart = (
    <>
      <PartTitle>自定义文本</PartTitle>
      <PartInfo>在下方输入文本，可直接「使用」，或「保存」为快捷按钮</PartInfo>
      <ItemList>
        <Button
          onClick={selectCurrentText}
          disabled={isSavingText || !content || isTextTooLong}
        >
          使用文本
        </Button>
        {!isSavingText ? (
          <Button
            className="btn-o"
            onClick={startSaveCurrentText}
            disabled={!content || isTextTooLong}
          >
            {saveTextBtnTitle}
          </Button>
        ) : (
          <>
            <Button className="btn-d" onClick={exitSavingCurrentText}>
              取消保存
            </Button>
          </>
        )}
      </ItemList>
      {isSavingText ? (
        <div className="step-info">
          <span>
            输入「文本名称」并选中需要的选项后，在「{'保存的文本'}」中选择「覆盖」或「添加」
          </span>
        </div>
      ) : null}

      {isSavingText ? (
        <>
          <div className="text-area with-top-sep">
            <PartInfo>
              文本名称&nbsp;&nbsp;
              <ErrorInfo hasError={isTextTitleTooLong}>
                {textTitle?.length || 0}
              </ErrorInfo>
              {`/${titleMaxLength}`}
              {!isTextTitleTooLong ? null : (
                <ErrorInfo hasError> 名称太长</ErrorInfo>
              )}
            </PartInfo>
            <input
              autoComplete="off"
              className="text-title-input"
              ref={inputTitleRef}
              placeholder={inputTitlePlaceHolder}
              onChange={handleTextTitleChange}
            />
          </div>
          <div className="text-area">
            <PartInfo>文本选项&nbsp;&nbsp;</PartInfo>
            <ItemList topPadding={0}>
              <CheckBox
                text={'乱序随机排列'}
                status={random}
                onChange={() => {
                  setRandom(!random);
                  setAuto(false);
                }}
              />
              {random ? (
                <CheckBox
                  text={'无限连续生成'}
                  status={auto}
                  onChange={() => setAuto(!auto)}
                />
              ) : null}
            </ItemList>
          </div>
        </>
      ) : null}
      <div className="text-area with-top-sep v-flex grow">
        <PartInfo>
          文本内容&nbsp;&nbsp;
          <ErrorInfo hasError={isTextTooLong}>{content.length || 0}</ErrorInfo>
          {`/${maxTextLength}`}
          {!isTextTooLong ? null : <ErrorInfo hasError> 文本太长</ErrorInfo>}
        </PartInfo>
        <textarea
          autoComplete="off"
          className="grow"
          disabled={isSavingText}
          ref={inputRef}
          placeholder={inputPlaceHolder}
          onChange={handleInputChange}
        />
      </div>
      <ItemList className="text-area">
        <Button
          className="btn-d"
          onClick={clearInput}
          disabled={isSavingText || !content}
        >
          清空
        </Button>
        <Button
          className="btn-o"
          onClick={inputCurrent}
          disabled={isSavingText || !!content}
        >
          写入当前文本
        </Button>
        <Button
          className="btn-o"
          onClick={randomResort}
          disabled={isSavingText || !content}
        >
          随机重排
        </Button>
        <Button
          className="btn-o"
          onClick={removeNonChinese}
          disabled={isSavingText || !content}
        >
          仅保留中文
        </Button>
      </ItemList>
    </>
  );

  return (
    <StyledTargetSetting>
      <GhostSeparatorV />
      {buildinPart(
        '内置文本',
        '其中「自动文本」为根据对应范围内的「慢速字」与「输入次数少的字」自动生成，「连续文本」为无限连续生成的「自动文本」；每次「切换」与「重新开始」时会重新生成文本'
      )}
      {/* <Separator /> */}
      {isSavingText || savedTextCard?.length > 0 ? (
        <>
          <GhostSeparatorV />
          {userSavedPart}
        </>
      ) : null}
      <GhostSeparatorV />
      {inputPart}
    </StyledTargetSetting>
  );
};
