import { getDataFromRaw } from 'utils/data';
import { getLSZiTimeData } from 'utils/storage';

export const randomSort = (_1: any, _2: any) => Math.random() - 0.5;

export const randomOrder = (arr: any[]) => {
  const newArr = [...arr];
  return newArr.sort(randomSort);
};

export const isChinese = (zi: string) => /[\u4e00-\u9fff]+/g.test(zi);

export const parseContent =
  (random = false) =>
  (str = '') => {
    const strArr = str.split('').filter((s) => !['\n', ' '].includes(s));
    if (!random) return strArr.join('');
    const randomArr = strArr.sort(randomSort);
    return randomArr.join('');
  };

export const rawText2ZiBricks = (str = '') => {
  const strArr = str.split('').filter((s) => !['\n', ' '].includes(s));
  return strArr;
};

export const filtBaseChinese =
  (placeholder = '') =>
  (str = '') => {
    const reg = /[^\u4e00-\u9fff]*/g;
    return str.replace(reg, placeholder);
  };

