import { useAutoAnimate } from 'hooks/animation-hooks';
import { useToggle } from 'hooks/common-hooks';
import { FC, useState, useEffect, useRef, useContext } from 'react';
import {
  Header_H,
  Color_Main_bg,
  Color_Sub_bg,
  Color_Sub_fg_1,
  Color_Sub_bg_1,
} from 'style/style-parameters';
import styled from 'styled-components';

export const BoxHeader = styled.div`
  box-sizing: border-box;
  height: ${Header_H};
  line-height: ${Header_H};
  color: ${Color_Sub_fg_1};
  background: ${Color_Sub_bg};
  border-bottom: solid 1px #0002;
  width: 100%;
  text-align: left;
  padding: 0 10px;
  position: relative;
  user-select: none;
  font-size: 12px;
  display: flex;
  .status-tip {
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    height: ${Header_H};
    width: 30px;
  }
`;

export const BoxContainer = styled.div<any>`
  background: ${Color_Sub_bg_1};
  flex-grow: ${p => p.last ? 1 : 0};
`;

export const BoxContent = styled.div<any>`
  /* 避免子元素margin的影响 */
  overflow: hidden;
  background: ${Color_Sub_bg_1};
  min-height: 60px;
`;

export const Box: FC<any> = ({
  notTarggleable = false,
  children,
  title,
  defaultShow = false,
  bottomBorder = false,
  hidden = false,
  last = false,
}) => {
  const [showContent, toggleShowContent] = useToggle(
    notTarggleable ? true : defaultShow
  );
  const handleTitleclick = () => {
    !notTarggleable && toggleShowContent();
  };
  const containerRef = useRef();
  useAutoAnimate(containerRef);
  return (
    <BoxContainer
      last={last}
      ref={containerRef}
      bottomBorder={bottomBorder}
      hidden={hidden}
    >
      {!title ? null : (
        <BoxHeader onClick={handleTitleclick}>
          {title}
          {notTarggleable ? null : (
            <div className="status-tip">{showContent ? '=' : '+'}</div>
          )}
        </BoxHeader>
      )}
      {showContent && <BoxContent>{children}</BoxContent>}
    </BoxContainer>
  );
};

export const EndBox = styled.div<any>`
  background: linear-gradient(
    ${({ up = false }) =>
      up ? `#0000, ${Color_Main_bg}` : `${Color_Main_bg}, #0000`}
  );
  height: ${(p) => p.height}px;
  border: solid 2px ${Color_Main_bg};
  border-width: ${({ up = false }) => (up ? '0 0 2px' : '2px 0 0')};
`;
