import { createContext, useState, useEffect } from 'react';

import {
  getLSZiTimeData,
  storeLSZiTimeData,
  getLSAppSetting,
  storeLSAppSetting,
  getLSTextCard,
  storeLSTextCard,
} from 'utils/storage';
import { useToggle } from 'hooks/common-hooks';
import { targetTextMap } from 'utils/text/text-cards';
import { iTextInfoCard } from 'utils/text/baker';

export const MainContext: any = createContext({});

const storedStates: any = {
  textCard: getLSTextCard(),
  ziTimeData: getLSZiTimeData(),
  appSetting: getLSAppSetting(),
};

export const WidthMainContext = ({ children }: any) => {
  const states = {
    textCard: useState<iTextInfoCard>(
      storedStates.textCard || targetTextMap.get('default')
    ),
    ziData: useState(storedStates.ziTimeData),
    currentText: useState<string>(''),
    appSetting: {
      enableAutoRestartToggle: useToggle(
        storedStates.appSetting?.enableAutoRestartToggle ?? false
      ),
      autoRestart: useToggle(
        (storedStates.appSetting?.enableAutoRestartToggle &&
          storedStates.appSetting?.autoRestart) ??
          false
      ),
      showSkipButton: useToggle(
        storedStates.appSetting?.showSkipButton ?? true
      ),
    },
  };

  // 存储文本信息
  useEffect(() => {
    storeLSTextCard(states.textCard[0]);
  }, [states.textCard[0]]);

  // 存储统计数据
  useEffect(() => {
    storeLSZiTimeData(states.ziData[0]);
  }, [states.ziData[0]]);

  // 存储设置
  useEffect(() => {
    storeLSAppSetting({
      enableAutoRestartToggle: states.appSetting.enableAutoRestartToggle[0],
      autoRestart: states.appSetting.autoRestart[0],
      showSkipButton: states.appSetting.showSkipButton[0],
    });
  }, [
    states.appSetting.enableAutoRestartToggle[0],
    states.appSetting.autoRestart[0],
    states.appSetting.showSkipButton[0],
  ]);

  return <MainContext.Provider value={states}>{children}</MainContext.Provider>;
};
