import { debounce } from 'lodash';
import { iTextInfoCard } from 'utils/text';
import { washZiTimeDataArray } from 'utils/data';
export const LSKeys = {
  ziTimeData: 'ziTimeData',
  ziTimeDataBK: 'ziTimeDataBK',
  targetString: 'targetString',
  targetID: 'targetID',
  progressNumber: 'progressNumber',
  skippedZiList: 'skippedZiList',
  customSavedText: 'customSavedText',
  lastUsedText: 'lastUsedText',
  appSetting: 'appSetting',
  textCard: 'textCard',
};

const getFromST = (key: string, defaultValue: any = null) => {
  let hasError = false;
  let obj = defaultValue;
  let string = '';
  try {
    string = localStorage.getItem(key) || '';
    obj = string ? JSON.parse(string) : defaultValue;
  } catch (e) {
    console.error('读取存储内容失败', e);
    hasError = true;
  }
  return [string, obj, hasError];
};

// 单字统计数据
export const getLSZiTimeData = () => {
  const [string, array, hasError] = getFromST(LSKeys.ziTimeData, []);
  const [washedArray, changed] = washZiTimeDataArray(array);
  if (changed || hasError) {
    // const [, bk] = getFromST(LSKeys.ziTimeDataBK);
    try {
      localStorage.setItem(
        LSKeys.ziTimeDataBK,
        JSON.stringify({ [`auto-bk-${Date.now()}`]: string })
      );
    } catch (e) {
      console.error(e);
    }
  }

  return new Map(washedArray);
};

export const storeLSZiTimeData = debounce(
  (map: Map<any, any>) => {
    try {
      localStorage.setItem(
        LSKeys.ziTimeData,
        JSON.stringify(Array.from(map.entries()))
      );
    } catch (e) {
      console.error(e);
    }
  },
  5 * 1000,
  { maxWait: 100 * 1000 }
);

// 跳过的字
export const getLSSkippedList = (): string[] => {
  const [_string, array, _hasError] = getFromST(LSKeys.skippedZiList, []);
  return array;
};
export const storeLSSkippedList = (ziList: string[]) => {
  try {
    localStorage.setItem(LSKeys.skippedZiList, JSON.stringify(ziList));
  } catch (e) {
    console.error(e);
  }
};
export const addSkippedZi = (ziList: string[]) => {
  const lastSkippedList = getLSSkippedList();
  storeLSSkippedList(Array.from(new Set([...lastSkippedList, ...ziList])));
};

// 用户保存的文本
export const getLSCustomSavedText = () => {
  const [_string, array, _hasError] = getFromST(LSKeys.customSavedText, []);
  return array.map((card: any) => {
    if (card.text?.bricks?.length > 0) {
      return card
    } else if (card.text && card.title) {
      return {
        id: '',
        title: card.title || '',
        text: {
          bricks: card.text?.split?.(''),
          brickType: 'zi',
        },
      }
    }
    return null;
  }).filter((card: any) => card);
};
export const storeLSCustomSavedText = (savedTextList: any[]) => {
  try {
    localStorage.setItem(LSKeys.customSavedText, JSON.stringify(savedTextList));
  } catch (e) {
    console.error(e);
  }
};

