import { ErrorBoundary } from 'components/_Error/error-boundary';
import { FC, useState } from 'react';
import {
  Color_Sub_bg,
  Color_Sub_bg_1,
  Color_Sub_fg_1,
  Header_H,
} from 'style/style-parameters';
import styled from 'styled-components';

const TabContainer = styled.div`
  flex-shrink: 1;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  background: ${Color_Sub_bg_1};
`;

const TabTitles = styled.div`
  display: flex;
  height: ${Header_H};
  flex-grow: 0;
  flex-shrink: 0;
`;

const tabBorderColor = '#ccc';
const tabBorderWidth = 1;
const TabTitle = styled.div<any>`
  flex-grow: 1;
  line-height: ${Header_H};
  color: ${Color_Sub_fg_1};
  background: ${(p) => (p.selected ? '#f2f2f2' : Color_Sub_bg)};
  font-size: 12px;
  font-weight: ${(p) => (p.selected ? 'bolder' : 'inherit')};
  user-select: none;
  box-sizing: border-box;
  border-left: solid ${tabBorderWidth / 2}px
    ${(p) => (p.first ? 'transparent' : tabBorderColor)};
  border-right: solid ${tabBorderWidth / 2}px
    ${(p) => (p.last ? 'transparent' : tabBorderColor)};
  border-bottom: solid ${tabBorderWidth}px ${tabBorderColor};
  /* ${(p) => (p.selected ? 'border-bottom: none;' : '')} */
  &:hover {
    cursor: pointer;
  }
`;

const TabContent = styled.div`
  flex-basis: 0;
  flex-shrink: 0;
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  & > * {
    flex-grow: 1;
  }
`;

export const Tabs: FC<any> = ({
  children,
  defaultIndex = 0,
}: {
  children: any[];
  defaultIndex: number;
}) => {
  const [selectedIndex, setSelectedIndex] = useState<number>(
    Math.max(0, Math.min(children.length - 1, defaultIndex))
  );
  return (
    <TabContainer>
      <TabTitles>
        {children.map((item: any, index: number) => {
          return (
            <TabTitle
              key={index}
              selected={index === selectedIndex}
              first={index === 0}
              last={index === children.length - 1}
              onClick={() => setSelectedIndex(index)}
            >
              {item.title}
            </TabTitle>
          );
        })}
      </TabTitles>
      <ErrorBoundary>
        <TabContent key={selectedIndex}>
          {children[selectedIndex].content}
        </TabContent>
      </ErrorBoundary>
    </TabContainer>
  );
};
