import { checkRawData, getZiAvgTime, getDataFromRaw } from 'utils/data';
import { randomOrder } from 'utils/tools';

export interface iTextInfo {
  bricks: string[];
  brickType?: 'zi' | 'ci';
  random?: boolean;
  length?: number;
  slowZiPercent?: number;
  slowZiLevel?: number;
  fallbackText?: string;
  infinity?: boolean;
}

export interface iTextInfoCard {
  id: string;
  text: iTextInfo;
  group?: string;
  title?: string;
  active?: boolean;
}

export const getSlowZiList = (
  bricks: string[],
  maxLength: number,
  percent = 0.5,
  ziTimeData?: any
): string[] => {
  // const listLength = Math.min(maxLength, Math.floor(ziTimeData.size * percent));
  const listLength = maxLength;
  let dataMapArr: any[][] = Array.from(ziTimeData);
  if (bricks.length > 0) {
    dataMapArr = dataMapArr.filter((data: any) => bricks.includes(data[0]));
  }
  return dataMapArr
    .sort(([, a], [, b]) => getZiAvgTime(b) - getZiAvgTime(a))
    .slice(0, listLength)
    .map(([zi]) => zi);
};

export const getLessTypedZiList = (
  bricks: string[],
  ziTimeData: any
): string[] => {
  return [...ziTimeData.entries()]
    .filter(
      (data: any) =>
        checkRawData(data[1]) && (!bricks ? true : bricks.includes(data[0]))
    )
    .sort((a: any, b: any) => getDataFromRaw(a).count - getDataFromRaw(b).count)
    .map((data: any) => data[0]);
};

export const bakeText = (
  {
    bricks,
    brickType = 'zi',
    random = false,
    length = 0,
    slowZiPercent = 0,
    slowZiLevel = 0.5,
    fallbackText = '',
    infinity = false,
  }: iTextInfo,
  ziTimeData: any
): string => {
  // 不随机直接按原顺序生成
  if (!random) {
    return bricks.join('');
  }
  // 随机词语不额外处理
  if (brickType === 'ci') {
    return randomOrder(bricks).join('');
  }

  // 不取子集时不额外处理
  if (!infinity && length <= 0) {
    return randomOrder(bricks).join('');
  }

  // 单字组合
  const newBricks = new Set();

  // 自动取字的配置
  const _length = infinity ? 20 : length
  const _slowZiPercent = infinity ? 0.7 : slowZiPercent;
  const _slowZiLevel = infinity ? 0.9 : slowZiLevel;

  // 添加慢速字
  if (_slowZiPercent) {
    const slowZiNumber = Math.min(_length, Math.floor(_length * _slowZiPercent));
    const slowZiBricks =
      _slowZiPercent <= 0
        ? []
        : getSlowZiList(bricks, slowZiNumber, _slowZiLevel, ziTimeData);
    slowZiBricks.forEach((brick: string) => newBricks.add(brick));
  }

  // 添加未练习字
  const lessTypedZiList = getLessTypedZiList(bricks, ziTimeData);
  const unTypedZiBricks: string[] = bricks
    .filter((brick: string) => !lessTypedZiList.includes(brick))
    .slice(0, _length - newBricks.size);
  unTypedZiBricks.forEach((brick) => newBricks.add(brick));

  // 添加少练习字
  const lessTypedZiBricks: string[] = lessTypedZiList.slice(
    0,
    _length - newBricks.size
  );
  lessTypedZiBricks.forEach((brick) => newBricks.add(brick));

  return randomOrder(Array.from(newBricks)).join('');
};
