import { DataBar, DataBox } from './style';
import { checkRawData, getDataFromRaw, getZiAvgTime } from 'utils/data';
import { buildInBricks } from 'utils/text/raw-texts';

const ziSet = [
  null,
  new Set(buildInBricks.zi_gb2312_1),
  new Set(buildInBricks.zi_3000.slice(0,1000)),
];

const intervals = [0, 0.3, 0.6, 1, 1.5, 2, 3, 5, 10, Infinity];

export const FrequencyGraph = ({ data }: { data: Map<string, any[]> }) => {
  const frequency = [
    Array(intervals.length - 1).fill(0),
    Array(intervals.length - 1).fill(0),
    Array(intervals.length - 1).fill(0),
  ];

  data.forEach((rawData: any[], zi) => {
    const time = getDataFromRaw(rawData).estimated;
    for (let i = 0; i < intervals.length - 1; i++) {
      if (time / 1000 <= intervals[i + 1]) {
        frequency[0][i] += 1;
        if (ziSet[2]?.has(zi)) {
          frequency[1][i] += 1;
          frequency[2][i] += 1;
          break;
        }
        if (ziSet[1]?.has(zi)) {
          frequency[1][i] += 1;
          break;
        }

        break;
      }
    }
  });

  const maxFrequency = Math.max(...frequency[0]);

  return (
    <DataBox>
      {frequency[0].map((count: number, index: number) => {
        const points = [
          (100 * frequency[2][index]) / maxFrequency, // 1000
          (100 * frequency[1][index]) / maxFrequency, // yiji
          (100 * frequency[0][index]) / maxFrequency, // 中日韩统一表意文字
        ];
        return (
          <DataBar key={index} progress={points}>
            {index === frequency[0].length - 1
              ? `> ${Number(intervals[index].toFixed(1))}s`
              : `${intervals[index + 1].toFixed(1)}s`}
          </DataBar>
        );
      })}
    </DataBox>
  );
};
