import { useState, useEffect, useRef, useContext } from 'react';

import { useTimeout, useUpdateEffect } from 'hooks/common-hooks';
import { MainContext } from 'context/main-context';
import { isChinese, isWindows } from 'utils/tools';
import { addSkippedZi } from 'utils/storage';

import { autoPauseTime } from './constans';
import {
  StyledTypingArea,
  TypingArea,
  TypingBoxInfo,
  TypingBoxTitle,
} from './style';
import { TypingBox } from './typing-box';
import { bakeText } from 'utils/text/baker';
import { calcNewZiTimeData, createRawData, getDataFromRaw } from 'utils/data';

const match = (target: string, progress: number, value: string) => {
  if (value.length <= 0) return;
  const valueShouldBe = target.slice(progress, progress + value.length);
  return value === valueShouldBe;
};

export interface iTypeTraining {}

export const TypeTraining = (_Args: iTypeTraining) => {
  const {
    textCard: [textCard],
    ziData: [ziDataMap, setZiData],
    currentText: [, setCurrentText],
    appSetting: {
      autoRestart: [autoRestart],
      showSkipButton: [showSkipButton],
    },
  } = useContext<any>(MainContext);
  const isInfinity = textCard?.text?.infinity;
  const targetStringContentRef = useRef<string>('');
  const getTargetText = () => {
    return bakeText(textCard.text, ziDataMap) || '';
  };
  const getTitle = () => textCard.title;
  let targetString = '';
  if (!isInfinity) {
    targetString = targetStringContentRef.current || getTargetText() || '';
  } else {
    targetString =
      targetStringContentRef.current ||
      Array(3)
        .fill('')
        .map(() => getTargetText())
        .join('');
  }
  targetStringContentRef.current = targetString;

  // 更新当前文本生成结果
  useEffect(() => {
    targetString && setCurrentText(targetString);
  }, [targetString]);

  const [inputContent = '', updateInputContent] = useState('');
  const [progressNumber, updateProgressNumber] = useState(0);
  const [matchedCount, updateMatchedCount] = useState(0);
  const [skipped, updateSkipped] = useState<number[]>([]);
  const [wordLength, updateWordLength] = useState<number[]>([]);
  const [lastMatchString, setLastMatchString] = useState('');
  const [inputCount, updateInputCount] = useState(0);
  const inputCountRef = useRef<any>(0);

  const [time, updateTime] = useState({
    beforePause: 0,
    lastStart: 0,
    lastMatch: 0,
  });
  const [inputTypeCount, updateInputTypeCount] = useState({
    zi: 0,
    ci: 0,
  });
  const [paused, tagglePaused] = useState(false);
  const setPaused = (pause: boolean, reset = false) => {
    tagglePaused(pause);
    if (pause) {
      inputCountRef.current = reset ? 0 : inputCount;
      reset && updateInputCount(0);
      setInfo('暂停');
    } else {
      setInfo('继续');
    }
  };
  const [info, setInfo] = useState('');

  const inputRef = useRef<any>();
  const inputContentRef = useRef<any>('');

  const pauseTimer = useTimeout(true);
  const clearInputTimer = useTimeout(false);

  const timePassed =
    time.beforePause +
    (time.lastStart && time.lastMatch ? time.lastMatch - time.lastStart : 0);
  const speed = (
    timePassed > 0
      ? (matchedCount - skipped.length) / (timePassed / (1000 * 60))
      : 0
  ).toFixed(1);
  const restNumber = targetString.length - progressNumber;
  const keySpeed = ((inputCount * 1000) / (timePassed || Infinity)).toFixed(1);
  const averageWordLength =
    wordLength.reduce((a: number, b: number) => a + b, 0) / wordLength.length ||
    0;

  // 输入法候选上屏时更新当前输入内容
  const handleCompositionEnd = () => {
    updateInputContent(inputContentRef.current);
  };

  const pause = () => {
    setPaused(true);
  };

  const clearInput = () => {
    if (isWindows) {
      clearInputTimer(() => {
        inputRef.current.value = '';
      });
    } else {
      inputRef.current.value = '';
    }
    inputContentRef.current = '';
    updateInputContent('');
  };

  const skip = () => {
    if (
      progressNumber >= targetString.length ||
      skipped.includes(progressNumber)
    ) {
      return;
    }
    updateSkipped([...skipped, matchedCount]);
    updateProgressNumber(progressNumber + 1);
    updateMatchedCount(matchedCount + 1);
    updateInputContent('');
    setLastMatchString('');
    addSkippedZi(
      targetString.split('').slice(progressNumber, progressNumber + 1)
    );
    clearInput();
  };

  const reset = () => {
    updateSkipped([]);
    updateProgressNumber(0);
    updateMatchedCount(0);
    updateInputCount(0);
    inputCountRef.current = 0;
    updateInputContent('');
    setPaused(false, true);
    updateTime({
      beforePause: 0,
      lastStart: 0,
      lastMatch: 0,
    });
    targetStringContentRef.current = '';
  };

  // 无限模式追加文本
  useEffect(() => {
    if (isInfinity && progressNumber > 30) {
      const newTextPart = getTargetText();
      targetStringContentRef.current =
        targetString.slice(newTextPart.length) + newTextPart;
      updateProgressNumber(progressNumber - newTextPart.length);
    }
  }, [progressNumber]);

  // 监听输入框的输入法候选状态
  useEffect(() => {
    const inputEle = inputRef.current;
    if (!inputEle) return;
    inputEle.addEventListener('compositionend', handleCompositionEnd);
    return () => {
      inputEle.removeEventListener('compositionend', handleCompositionEnd);
    };
  }, []);

  // 更换文本后重置
  useUpdateEffect(() => {
    reset();
  }, [textCard]);

  // 暂停后更新时间并清空输入框
  useEffect(() => {
    if (paused === true) {
      updateTime({
        beforePause: timePassed,
        lastStart: 0,
        lastMatch: 0,
      });
      clearInput();
    }
  }, [paused]);

  // 自动重开
  useEffect(() => {
    if (!autoRestart || progressNumber < targetString.length) return;
    reset();
    setTimeout(() => {
      inputRef?.current?.focus?.();
    });
  }, [progressNumber === targetString.length]);

  const handleInputChange = (event: any) => {
    const now = Number(Date.now());
    const value = event.target.value;

    // 删除不计入按键次数
    if (event.nativeEvent.inputType !== 'deleteContentBackward') {
      inputCountRef.current++;
    }

    // 候选状态时不更新当前输入内容
    inputContentRef.current = value;
    if (event.nativeEvent.inputType !== 'insertCompositionText') {
      updateInputContent(value);
    }

    // 输入四个空格触发跳过
    if (value === '    ') {
      skip();
    }

    let lastStart = 0;
    let lastMatch = 0;

    // 开始计时
    if (!time.lastStart || paused) {
      setPaused(false);
      lastStart = now;
    }

    // 输入内容匹配时更新进度（ui变化主要发生在这里）
    const isMatch =
      value !== '    ' && match(targetString, progressNumber, value);
    if (isMatch) {
      lastMatch = now;
      // 更新匹配值记录
      setLastMatchString(value);
      // 更新进度值
      updateProgressNumber(progressNumber + value.length);
      updateMatchedCount(matchedCount + value.length);
      // 更新字词计数，用于计算「打词率」
      updateInputTypeCount({
        zi: inputTypeCount.zi + (value.length === 1 ? 1 : 0),
        ci: inputTypeCount.ci + (value.length === 1 ? 0 : 1),
      });
      // 更新键入次数
      updateInputCount(inputCountRef.current);
      // 更新词长列表
      updateWordLength([...wordLength, value.length]);
      // 更新单字输入速度数据
      const passTime = Number(
        now - (time.lastMatch || time.lastStart || lastStart)
      );
      const cList = value.split('');
      const newData = new Map(ziDataMap || []);
      cList.forEach((zi: string) => {
        const ziTime = passTime / value.length;
        if (!isChinese(zi) || ziTime > 1000 * 60) return;
        const lastData = getDataFromRaw(ziDataMap?.get(zi));
        newData.set(
          zi,
          createRawData(calcNewZiTimeData(lastData, ziTime, now))
        );
      });
      setZiData(newData);
      // 清空当前输入内容
      clearInput();
    }

    // 更新时间记录
    updateTime({
      ...time,
      // 开始或继续时才会更新lastStart
      lastStart: lastStart || time.lastStart,
      // 匹配的情况才会更新lastMatch
      lastMatch: lastMatch || time.lastMatch,
    });

    // 设置自动暂停计时
    pauseTimer(() => {
      setPaused(true);
    }, autoPauseTime);
  };

  const title = (
    <TypingBoxTitle>
      <div className="btn-0" onClick={reset}>
        重新开始
      </div>
      {showSkipButton && restNumber > 0 ? (
        <div className="btn-0" onClick={skip}>
          跳过单字
        </div>
      ) : null}
      <div className="text-title">
        {getTitle() || `共 ${targetString.length} 字的文本`}
      </div>
    </TypingBoxTitle>
  );

  const subTitle = (
    <TypingBoxInfo>
      {autoRestart ? <div>自动重开</div> : null}
      {isInfinity ? (
        <div className="item-1">连续模式已输入 {matchedCount} 字</div>
      ) : (
        <div className="item-1">
          {restNumber > 0 ? `余 ${restNumber} 字` : '已完成'}
        </div>
      )}
      <div
        className="item-1"
        title="平均每分钟打字的个数"
      >{`字速: ${speed}`}</div>
      <div title="平均每次上屏字数">{`词长: ${averageWordLength.toFixed(
        1
      )}`}</div>
      <div title="平均每秒输入次数">{`键速: ${keySpeed}`}</div>
    </TypingBoxInfo>
  );

  const devInfo = (
    <div
      id="dev-info"
      style={{
        display: 'none',
      }}
    >
      info: {info}
      <br />
      inputContent: {inputContent}
      <br />
      input count: {inputCountRef.current} =={'>'} {inputCount}
      <br />
      time: {time.beforePause.toFixed(1)} +{' '}
      {(time.lastMatch - time.lastStart).toFixed(1)} = {timePassed.toFixed(1)}
      <br />
      progressNumber: {progressNumber}
      <br />
    </div>
  );

  return (
    <StyledTypingArea>
      {title}
      {subTitle}
      <TypingArea>
        <TypingBox
          progressNumber={progressNumber}
          targetString={targetString}
          skippedList={skipped}
          onPause={pause}
          inputContent={inputContent}
          lastMatchString={lastMatchString}
          paused={paused}
          inputRef={inputRef}
          handleInputChange={handleInputChange}
          isInfinity={isInfinity}
        />
        {devInfo}
      </TypingArea>
    </StyledTypingArea>
  );
};
