import styled from 'styled-components';

const StyledCheckBox = styled.label`
  user-select: none;
  min-height: 20px;
  font-size: 14px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 4px 0;
  color: #000a;
  text-align: left;
  margin-right: 10px;
  input {
    margin-right: 8px;
    vertical-align: middle;
  }
`;

export const CheckBox = ({
  onChange = (e: any): void => {},
  status = false,
  text = '',
}) => {
  return (
    <StyledCheckBox>
      <input type="checkbox" onChange={onChange} checked={status} />
      {text}
    </StyledCheckBox>
  );
};

export const Radio = ({ text = '' }) => {
  return (
    <StyledCheckBox>
      <input type="radio" />
      {text}
    </StyledCheckBox>
  );
};
